import { Package, GSP } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector } from '@angular/core';
import { ControlMetadataPackageView } from './controlmetadata/controlmetadata.packageview';

export class ControlMetadataPackage extends Package {
    constructor(
        gsp: GSP,
        private cfr: ComponentFactoryResolver,
        private injector: Injector
    ) {
        super(gsp);
    }

    activate(state) {
        this.gsp.workspace.addOpener((uri: string) => {
            return this.openCtrl(uri);
        });
    }


    /**
     * 打开控件元数据视图
     * @param uri 文件地址
     */
    openCtrl(uri) {
        if (uri.endsWith('.wcm')) {
            const controlMetadataPackView = new ControlMetadataPackageView({}, this.cfr, this.injector);
            // tslint:disable-next-line:no-unused-expression
            controlMetadataPackView.getElement().instance.uri = uri;
            return {
                getTitle: () => '控件元数据 - ' + uri.slice(uri.lastIndexOf('/') + 1),
                getElement: () => controlMetadataPackView.getElement()
            };
        }
    }
}



