import { PackageView } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector } from '@angular/core';
import { ControlmetadataComponent } from './controlmetadata.component';
export class ControlMetadataPackageView extends PackageView {

    constructor(serializedState: any, private cfr: ComponentFactoryResolver, private injector: Injector) {
        super(serializedState);
        const componentFactory = this.cfr.resolveComponentFactory(ControlmetadataComponent);
        this.element = componentFactory.create(this.injector);
    }
}



