import { Component, OnInit } from '@angular/core';
import { MetadataDto, GSPMetadataService } from '@gsp-lcm/metadata-selector';
import { CacheService } from '@farris/ide-devkit/services/cache';

@Component({
  selector: 'app-controlmetadata',
  templateUrl: './controlmetadata.component.html',
  styleUrls: ['./controlmetadata.component.css']
})
export class ControlmetadataComponent implements OnInit {

  public metadataDto: MetadataDto;
  public content;
  private sessionId: string;
  options = {
    theme: 'vs-dark',
    language: 'json'
  };



  /**
   * Getter uri
   * @return {string}
   */
  public get uri(): string {
    return this._uri;
  }

  /**
   * Setter uri
   * @param {string} value
   */
  public set uri(value: string) {
    this._uri = value;
    const arr = value.split('\/');
    let path = '\/projects';
    for (let i = 1; i < arr.length - 1; i++) {
      path = path + '\/' + arr[i];
    }
    this.getMetadata(arr[arr.length - 1], path);
  }


  /**
 *  文件相对路径
 */
  private _uri: string;

  constructor(private metadataService: GSPMetadataService, private cache: CacheService) {
    this.sessionId = this.cache.get('sessionId');
  }


  /**
   * 保存修改后的控件元数据
   */
  saveForm() {
    this.metadataDto.content = this.content;
    this.metadataService.SaveMetadata(this.metadataDto, this.sessionId).subscribe(data => {
      alert('保存成功！');
    },
      error => {
        alert('保存失败！');
      }
    );
  }


  /**
   * 获取元数据信息
   */
  getMetadata(fullname?: string, path?: string) {
    this.metadataService.LoadMetadata(fullname, path).subscribe(
      data => {
        this.metadataDto = data;
        this.content = data.content;
      }
    );
  }

    /**
   * 代码编辑器自动格式化
   * @param event 打开编辑器事件
   */
  onInitHandler(event: any) {
    setTimeout(() => {
      event.editor.getAction('editor.action.formatDocument').run().then(null);
    }, 200);
  }

  ngOnInit() {
  }
}



